<template>
  <div>
    <!-- 查询 -->
    <common-wrapper>
      <search class="search" :warehouseList="warehouseList" :merchantsList="merchantsList" @getmerchants="getMerchantsList" @search="queryList" @rest="rest" />
    </common-wrapper>
    <common-wrapper>
    <el-tabs v-model="taskType" @tab-click="changeTask">
      <el-tab-pane label="收货任务">
        <common-table  :flex="230">
          <template slot="table">
            <el-table
              ref="taskTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="entryData">
              <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
              <el-table-column prop="account" label="入库单号" sortable></el-table-column>
              <el-table-column prop="merchants" label="商户" sortable></el-table-column>
              <el-table-column prop="phone" label="联系电话"></el-table-column>
              <el-table-column prop="arriveDate" label="送达日期" sortable></el-table-column>
              <el-table-column prop="state" label="状态" ></el-table-column>
              <el-table-column prop="warehouseName" label="所属仓库" sortable></el-table-column>
              <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination[0]">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination[0].pageNum"
                             :page-size="pagination[0].pageSize"
                             :total="pagination[0].total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </el-tab-pane>
      <el-tab-pane label="收货质检任务">
        <common-table  :flex="230">
          <template slot="table">
            <el-table
              ref="taskTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="entryQualityData">
              <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
              <el-table-column prop="account" label="入库单号" sortable></el-table-column>
              <el-table-column prop="merchants" label="商户" sortable></el-table-column>
              <el-table-column prop="phone" label="联系电话" sortable></el-table-column>
              <el-table-column prop="arriveDate" label="送达日期" sortable></el-table-column>
              <el-table-column prop="state" label="状态"></el-table-column>
              <el-table-column prop="warehouseName" label="所属仓库" sortable></el-table-column>
              <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination[1]">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination[1].pageNum"
                             :page-size="pagination[1].pageSize"
                             :total="pagination[1].total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </el-tab-pane>
      <el-tab-pane label="上架任务">
        <common-table  :flex="230">
          <template slot="table">
            <el-table
              ref="taskTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="upShelvesData">
              <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
              <el-table-column prop="account" label="入库单号" sortable></el-table-column>
              <el-table-column prop="merchants" label="商户" sortable></el-table-column>
              <el-table-column prop="phone" label="联系电话" sortable></el-table-column>
              <el-table-column prop="arriveDate" label="送达日期" sortable></el-table-column>
              <el-table-column prop="state" label="状态"></el-table-column>
              <el-table-column prop="warehouseName" label="所属仓库" sortable></el-table-column>
              <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination[2]">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination[2].pageNum"
                             :page-size="pagination[2].pageSize"
                             :total="pagination[2].total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </el-tab-pane>
      <el-tab-pane label="备货任务">
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="taskTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="deliveryData">
              <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
              <el-table-column prop="account" label="出库单号" sortable></el-table-column>
              <el-table-column prop="merchants" label="商户" sortable></el-table-column>
              <el-table-column prop="customerName" label="客户姓名" sortable></el-table-column>
              <el-table-column prop="customerAddress" label="客户地址" min-width="200"></el-table-column>
              <el-table-column prop="customerPhone" label="客户电话" sortable></el-table-column>
              <el-table-column prop="arriveDate" label="送达日期" sortable></el-table-column>
              <el-table-column prop="state" label="状态"></el-table-column>
              <el-table-column prop="warehouseName" label="所属仓库" sortable></el-table-column>
              <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination[3]">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination[3].pageNum"
                             :page-size="pagination[3].pageSize"
                             :total="pagination[3].total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </el-tab-pane>
      <el-tab-pane label="出库质检任务">
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="taskTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="deliveryQualityData">
              <!--<el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>-->
              <el-table-column prop="account" label="入库单号" sortable></el-table-column>
              <el-table-column prop="merchants" label="商户" sortable></el-table-column>
              <el-table-column prop="phone" label="联系电话" sortable></el-table-column>
              <el-table-column prop="arriveDate" label="送达日期" sortable></el-table-column>
              <el-table-column prop="state" label="状态"></el-table-column>
              <el-table-column prop="warehouseName" label="所属仓库" sortable></el-table-column>
              <el-table-column prop="remarks" label="备注"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination[4]">
            <el-pagination   background   layout="total, prev, pager, next"
                             :current-page.sync="pagination[4].pageNum"
                             :page-size="pagination[4].pageSize"
                             :total="pagination[4].total"
                             @current-change="changePage"
            ></el-pagination>
          </template>
        </common-table>
      </el-tab-pane>
    </el-tabs>
    </common-wrapper>
  </div>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import CommonTable from '@/components/commonTable'
  import NavButton from '@/components/navButton'
  import Search from './Components/Search'
  import { setSession, getSession } from '@/utils/session'

  import { getAllMerchantsList } from '@/api/common'
  import { getTask } from '@/api/warehouse'
  import {mixins} from '@/mixins'

  export default {
    name: 'TaskPool',
    mixins: [mixins],
    data () {
      return {
        warehouseList: this.$store.getters.warehouseList,
        merchantsList: [],
        entryData: [],
        entryQualityData: [],
        upShelvesData: [],
        deliveryData: [],
        deliveryQualityData: [],
        isListLoading: false,
        pagination0: { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
        },
        pagination: [// 翻页数据
          {
            pageNum: 1,
            pageSize: 10,
            total: 0
          },
          {
            pageNum: 2,
            pageSize: 10,
            total: 0
          },
          {
            pageNum: 3,
            pageSize: 10,
            total: 0
          },
          {
            pageNum: 4,
            pageSize: 10,
            total: 0
          },
          {
            pageNum: 5,
            pageSize: 10,
            total: 0
          }],
        taskType: '0'
      }
    },
    mounted () {

    },
    methods: {
      // 商户列表
      getMerchantsList (params) {
        getAllMerchantsList(params).then(res => {
          if(!res.error) {
            this.merchantsList = res
          }
        })
      },
      changeTask (tab) {
        this.taskType = tab.index
        this.queryList(this.searchParams)
      },
      // 查询和初始化查询
      queryList (params) {
        const searchParams = this.searchParams = params
        this.getOrders(
          {
            ...searchParams,
            taskType: this.taskType,
            pageNum: this.pagination[this.taskType].pageNum,
            pageSize: this.pagination[this.taskType].pageSize
          }
        )
      },
      rest (params) {
        const searchParams = this.searchParams = params
        this.getOrders(
          {
            ...searchParams,
            taskType: this.taskType,
            pageNum: this.pagination[this.taskType].pageNum,
            pageSize: this.pagination[this.taskType].pageSize
          }
        )
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination[this.taskType].pageNum = pageNum
        this.queryList(this.searchParams)
      },
      // 生成列表序号
      indexMethod (index) {
        return (index + 1) + (this.pagination[this.taskType].pageNum - 1) * this.pagination[this.taskType].pageSize
      },
      getOrders (params) {
        this.isListLoading = true
        getTask(params).then(res => {
          if(!res.error){
            switch (this.taskType){
              case '0':
                this.entryData=res.item[0]
                break
              case '1':
                this.entryQualityData=res.item[1]
                break
              case '2':
                this.upShelvesData=res.item[2]
                break
              case '3':
                this.deliveryData=res.item[3]
                break
              case '4':
                this.deliveryQualityData=res.item[4]
                break
            }
            this.pagination[this.taskType].total = res.total
            this.pagination[this.taskType].pageNum = res.pageNum
            this.pagination[this.taskType].pageSize = res.pageSize
            this.isListLoading = false
          }
        })
      }
    },
    components: {
      CommonWrapper,
      CommonTable,
      NavButton,
      Search
    }
  }
</script>

<style scoped>

</style>



