<template>
  <el-form :model="searchForm" ref="searchForm"  label-width="50px" >
    <el-row>
      <el-col :span="5">
        <el-form-item label="单号"  prop="account">
          <el-input v-model="searchForm.account"  clearable placeholder="请输入单号" ></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="仓库" prop="warehouseUuid">
          <el-select v-model="searchForm.warehouseUuid" @change="warehouseChange" filterable placeholder="请选择仓库">
            <el-option
              v-for="(item, index) in warehouseList"
              :key="index"
              :label="item.warehouseName"
              :value="item.warehouseUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="商户"  prop="merchants">
          <el-select v-model="searchForm.merchants" filterable clearable placeholder="请选择商户">
            <el-option
              v-for="(item, index) in merchantsList"
              :key="index"
              :label="item.userName"
              :value="item.userUuid"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="日期"  prop="timeRange">
          <el-date-picker
            class="w100p"
            placeholder="请选择日期"
            v-model="searchForm.date"
            unlink-panels
            clearable
            size="mini"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :span="4" class="flex-right">
        <el-button type="primary" @click.native="search">查询</el-button>
        <el-button @click="resetForm('searchForm')">重置</el-button>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
  import { setSession, getSession } from '@/utils/session'
  export default {
    name: 'Search',
    data () {
      return {
        searchForm: {
          account:'',
          warehouseUuid:'',
          merchants:'',
          date:''
        }
      }
    },
    props: {
      merchantsList: {
        type: Array
      },
      warehouseList: {
        type: Array
      }
    },
    mounted () {
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.search()
      this.$emit('getmerchants',{warehouseUuid:this.searchForm.warehouseUuid})
    },
    methods: {
      warehouseChange (value) {
        setSession('warehouseUuid',value)
      },
      // 检索
      search () {
        this.$emit('search', Object.assign({}, this.searchForm))
      },
      // 重置
      resetForm (formName) {
        this.$refs[formName].resetFields()
        this.searchForm.warehouseUuid = getSession('warehouseUuid')
        this.$emit('rest', Object.assign({}, this.searchForm))
      }
    }
  }
</script>

<style scoped>

</style>



